.header__nav {
  clear: both;
  /* height: 99px; */
  background: #63c5c3;
  text-align: center;
  z-index: 900;
  width: 100%;
}

@media only screen and (max-width: 40em) {
  .header__nav {
    position: fixed;
    bottom: 0px;
    width: 100%;
  }
}

.nav__list {
  list-style: none;
  margin: 0px;
  display: table;
  padding: 0px;
  width: 100%;
}

.nav__item--active {
  background: #3b9f9d;
}
