  content: "";
  position: absolute;
  margin-top: 10px;
  left: 46%;
  height: 25.5px;
  width: 20px;
}*/

.nav__item:hover {
  background: #2bb9b7;
}
.nav__item {
  display: table-cell;
  padding: 20px 25px 13px;
  position: relative;
}

@media only screen and (max-width: 40em) {
  .nav__item {
    padding: 2px 0px 13px;
  }
}

.nav__link {
  color: #fff;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  text-align: center;
  text-decoration: none;
}
.nav__icon--projects {
  margin: 0px auto;
  width: 40px;
  height: 40px;
  background: url(../../site_media/Images/Menu_Projects.png);
  background-repeat: no-repeat;
  background-size: contain;
}

.nav__icon--homepage {
  margin: 0px auto;
  width: 60px;
  height: 60px;
  background: url(../../site_media/Images/Menu_UC.png);
  background-repeat: no-repeat;
  background-size: contain;
  overflow: hidden;
}
.nav__icon--spotlight {
  margin: 0px auto;
  width: 40px;
  height: 40px;
  background-image: url(../../site_media/Images/Menu_Spotlight.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.nav__icon--friends {
  margin: 0px auto;
  width: 40px;
  height: 40px;
  background: url(../../site_media/Images/Menu_Friends.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.nav__icon--yearbook {
  margin: 0px auto;
  width: 40px;
  height: 40px;
  background: url(../../site_media/Images/Menu_Photos.png);
  background-repeat: no-repeat;
  background-size: contain;
}
